@font-face {
    font-family: 'Nekst-Black';
    font-weight: 900;
    font-style: normal;
    src: url('./Nekst-Black.eot');
     src: 
     local('./Nekst Black'), local('./Nekst-Black'),
        url('./Nekst-Black.eot?#iefix') format('embedded-opentype'),
        url('./Nekst-Black.woff2') format('woff2'),
        url('./Nekst-Black.woff') format('woff'),
        url('./Nekst-Black.ttf') format('truetype');
}

@font-face {
    font-family: 'Gilroy';
    font-weight: normal;
    font-style: normal;
    src: url('./Gilroy-Regular.eot');
    src: 
    local('./Gilroy Regular'), local('./Gilroy-Regular'),
        url('./Gilroy-Regular.eot?#iefix') format('embedded-opentype'),
        url('./Gilroy-Regular.woff2') format('woff2'),
        url('./Gilroy-Regular.woff') format('woff'),
        url('./Gilroy-Regular.ttf') format('truetype');
}